import React from 'react'
import {useRoutes} from 'hookrouter';
import { Home, Postcode, NotFound, News, Parliament, Members, Social, Images } from 'Containers'
import './App.scss'
const routes = {
    '/': () => <Social />,
    '/postcode': () => <Postcode />,
    '/members': () => <Members />,
    '/govt': () => <Parliament />,
    '/news': () => <News />,
    '/images': () => <Images />,
};
  
const MyApp = () => {
    const routeResult = useRoutes(routes);
    return routeResult || <NotFound />;
}

export default MyApp