import React, { useContext, useEffect, useState } from 'react'
import './style.scss'
import PartyColors from 'Data/partyColors'



const Vote = (props) => {


	function getDate(date){
		let d = new Date(date)
		return d.toUTCString();
	}
	return (
		
	
		<div className="ArticleVoteContainer">
			<p className="title">{props.Title}</p>
			<p className="date">{getDate(props.Date)}</p>
			<div className="tellersContainer">
				<p className="title">For <span>({props.AyeCount})</span></p>
				<div className="row">
				{props.Ayes && props.Ayes.map((item, i) => {
					
					return(
						<div key={i}className="teller" style={{backgroundColor:'#' +PartyColors[item.PartyName] + '44'}}>
						<p className="partyName">{item.PartyName}</p>
						<p className="partyVoteCount">{item.VoteCount}</p>
						</div>
					)
				})}
				</div>
			</div>
			<div className="tellersContainer">
				<p className="title">Against <span>({props.NoCount})</span></p>
				<div className="row">
				{props.Noes && props.Noes.map((item, i) => {
					return(
						
						<div key={i}className="teller" style={{backgroundColor:'#' +PartyColors[item.PartyName] + '44'}}>
						<p className="partyName">{item.PartyName}</p>
						<p className="partyVoteCount">{item.VoteCount}</p>
						</div>
						
					)
				})}
				</div>
			</div>
		</div>

	)
    
}

export default Vote