import React, { useState, useRef } from "react";
import { Provider } from "./Contexts/global";
import initialState from './initialState'
import Routes from './routes'
import { GridView, Menu, Article} from './Components'
import $ from 'jquery'

const host ='/api'

const ProviderComponent = props => {  
  let currentDroppable = useRef(null).current
  const [dragging, setDragging] = useState();
  const [article, setArticle] = useState([])
  const [isDroppable, setIsDroppable] = useState(false)
  

  function onMouseMove(event){
    let elemBelow = document.elementFromPoint(event.clientX, event.clientY);
    if (!elemBelow) return;
    let droppableBelow = elemBelow.closest('.droppable');
    if (currentDroppable != droppableBelow) {
      // if (currentDroppable) {
      //   console.log('setting droppable to false')
      //   setIsDroppable(false)
      // } 
      
      currentDroppable = droppableBelow
      if (currentDroppable) {
        setIsDroppable(true)
      }
    }
  }

  function onDragEnd(){
    
    // setCurrentDroppable(null)
    console.log('dropping', isDroppable)
    if (isDroppable){
      console.log('dropping', dragging, article)
        setIsDroppable(false)

        switch(dragging.type){
          case 'member':
            return getMember(dragging.item, dragging.meta)
          default: 
            return updateArticle(dragging)
        }
    }
  }

  function getMember(id, meta){
    $.get(`${host}/memberbyid?id=${id}` , (res) => {
      let newMember = {
        ...res.value,
        ...meta
      }
      console.log('got member', id, meta, newMember)
      updateArticle({
        type: 'member',
        item: newMember
      })
    })
  }


  function updateArticle(item){
     let newArticle = Object.assign([], article)
        newArticle.push(item)
        console.log('newArticle', newArticle)
        setArticle(newArticle)
  }

  function onDragStart(item){
    console.log('dragging', item)
    setDragging(item)
    return false

  }

  function onDragMouseDown(){
    console.log('mouse down')
    document.addEventListener('drag', onMouseMove);
  }

  function onDragMouseUp(){
    console.log('mouse up')
    document.removeEventListener('drag', onMouseMove);
    
  }

  return (
     <Provider
        value={{
          host: host,
          onDragStart,
          onDragEnd,
          currentDroppable,
          onDragMouseDown,
          onDragMouseUp,
          isDroppable,
          article
        }}
      >
      
       {/*<GridView />*/}
       <div className="appContentContainer">
       <Menu />
       <div className="routesContainer">
          <Routes />
        </div>
       <Article />
       </div>
    </Provider>
  );
};

export default ProviderComponent;